<template>
    <div class="integral-list">
        <div class="point-head">
            <section-title class="left" v-bind:tit="$L('当前积分可兑')+'：'"></section-title>
            <span class="right">{{ $L('当前可用积分') }}： {{tableData.pay_points}}</span>
        </div>
        <div class="list">
            <tab-box :top-menu="topMenu" class="top-menu"></tab-box>
            <div class="list-item" v-if="tableData.hasOwnProperty('exchangeList') && tableData.exchangeList.length>0">
                <div class="content-item">
                    <ul class="content-ul">
                        <li class="content-list" v-for="(item,index) in tableData.exchangeList" v-bind:key="index">
                            <integral-style-one v-bind:data="item"></integral-style-one>
                        </li>
                    </ul>
                </div>
                <div class="list-pages">
                    <el-pagination
                            background
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[10,20,30,40]"
                            :page-size="pageSize"
                            layout="prev, pager, next, total, jumper"
                            :total="tableData.allNum">
                    </el-pagination>
                </div>
            </div>
            <div class="list-none" v-else>

                {{ $L('暂无数据') }}

            </div>
        </div>
    </div>
</template>

<script>
    import SectionTitle from "../../../components/common/sectionTitle";
    import IntegralStyleOne from "../../../components/point/integralStyleOne";
    import TabBox from "../../../components/layout/web/tabBox";
    export default {
        name: "integralList",
        components: {
            TabBox,
            SectionTitle,
            IntegralStyleOne
        },
        data () {
            return {
                topMenu:{
                    'menuList': [
                        {
                            text: this.$L('全部'),
                            router: 'integralList',
                            params: {
                                points: 'all'
                            }
                        },
                        {
                            text: this.$L('1-99'),
                            router: 'integralList',
                            params: {
                                points: 'one'
                            }
                        },
                        {
                            text: this.$L('100-499'),
                            router: 'integralList',
                            params: {
                                points: 'two'
                            }
                        },
                        {
                            text: this.$L('500-4999'),
                            router: 'integralList',
                            params: {
                                points: 'three'
                            }
                        },
                        {
                            text: '5000'+this.$L('以上'),
                            router: 'integralList',
                            params: {
                                points: 'four'
                            }
                        },
                    ]
                },
                currentPage:1,
                pageSize:6,
                tableData: {},
                points: 'all',
                type: ''
            }
        },
        beforeRouteUpdate (to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.getData(to);
            next();
        },
        created (){

            this.getData();

        },
        methods : {
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            getData(router) {
                const _self = this;
                _self.loading = true;
                if(!router){
                    router = _self.$route;
                }
                _self.points = router.params.hasOwnProperty('points') ? router.params.points : 'all';
                _self.type = router.hasOwnProperty('params') && router.params.hasOwnProperty('type') ? this.$route.params.type : 'all';
                let arr = {
                    p: _self.currentPage,
                    num: _self.pageSize,
                    type: _self.type,
                    points: _self.points,
                };
                _self.$api('exchangeList', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'integralShop'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name:'integralShop'});
                    });
                });
            },
        }
    }
</script>

<style lang="scss" scoped>
    .integral-list{
        position: absolute;
        width: 100%;
        height: auto;
        top: 0;
        left: 0;
        padding: 20px;
        .point-head{
            height: 38px;
            .left{
                float: left;
            }
            .right{
                font-size: 14px;
                float: right;
                display: inline-block;
                height: inherit;
                line-height: 28px;

            }
        }
        .top-menu{
            top: 48px;
           /deep/ .tab-head{
                width: calc(100% - 40px);
               margin: 0 20px;
            }
        }
        .list{
            width: 100%;
            .list-item{
                position: absolute;
                top: 98px;
                width: calc(100% - 40px);
                margin-bottom: 20px;
                .content-item{
                    position: relative;
                    background: #fff;
                    .content-ul{
                        display: flex;
                        justify-content: flex-start;
                        box-sizing: border-box;
                        width: 100%;
                        text-align: left;
                        padding: 20px 20px 0;
                        .content-list{
                            width: calc((100% - 100px)/6);
                            margin: 0 20px 20px 0;
                        }
                    }
                }
                .list-pages{
                    background: #fff;
                    text-align: center;
                    padding: 40px 0;
                }
            }
            .list-none{
                position: absolute;
                top: 98px;
                width: calc(100% - 40px);
                margin-bottom: 20px;
                height: 200px;
                color: #999;
                font-size: 16px;
                line-height: 200px;
                text-align: center;
                background: #fff;
            }
        }
    }
</style>